import React from 'react'
import styled from 'styled-components'
import Carousel from 'react-slick'
import {Grid, Row, Col} from 'react-flexbox-grid'
import {
  Card,
  CardActions,
  CardHeader,
  CardMedia,
  CardTitle,
  CardText
} from 'material-ui/Card';
import FlatButton from 'material-ui/FlatButton';
import RaisedButton from 'material-ui/RaisedButton';

import ArrowBack from 'material-ui/svg-icons/hardware/keyboard-arrow-left';
import ArrowForward from 'material-ui/svg-icons/hardware/keyboard-arrow-right';

import Drawer from 'material-ui/Drawer';
import Chip from 'material-ui/Chip';

import {List, ListItem} from 'material-ui/List';
import ActionGrade from 'material-ui/svg-icons/action/grade';
import Subheader from 'material-ui/Subheader';
import _ from 'lodash';

import {Link} from 'components';

const Wrapper = styled.div `

`
class FileSearchMenu extends React.Component {

  constructor(props) {
    super(props)
    this.state = {
      open: true,
      schema: [
        {
          'category': 'Grupos Biológicos',
          'items': [
            {
              name: 'Anfibios',
              value: 1634
            }, {
              name: 'Aves',
              value: 2755
            }, {
              name: 'Hongos',
              value: 3345
            }, {
              name: 'Insectos',
              value: 4355
            }, {
              name: 'Mamíferos',
              value: 5674
            }, {
              name: 'Peces',
              value: 6325
            }, {
              name: 'Plantas',
              value: 73577
            }, {
              name: 'Reptiles',
              value: 8455
            }
          ]
        }, {
          'category': 'Departamentos',
          'items': [
            {
              name: 'Cundinamarca',
              value: 1832
            }, {
              name: 'Boyacá',
              value: 2124
            }
          ]
        }, {
          'category': 'Ecosistemas',
          'items': [
            {
              name: 'Bosque tropical',
              value: 135565
            }, {
              name: 'Desierto',
              value: 2456
            }, {
              name: 'Bosque seco',
              value: 33466
            }, {
              name: 'Páramo',
              value: 46225
            }, {
              name: 'Bosque de niebla',
              value: 51123
            }, {
              name: 'Humedales',
              value: 665433
            }
          ]
        }
      ],
      chipData: [
        {
          value: 0,
          name: 'Angular'
        }, {
          value: 1,
          name: 'JQuery'
        }, {
          value: 2,
          name: 'Polymer'
        }, {
          value: 3,
          name: 'ReactJS'
        }
      ]
    };

    this.styles = {
      chip: {
        margin: 4
      },
      wrapper: {
        display: 'flex',
        flexWrap: 'wrap'
      }
    };
  }

  handleRequestDelete = (data) => {
    this.chipData = this.state.chipData;
    const chipToDelete = this.chipData.map((chip) => chip.value).indexOf(data.value);
    this.chipData.splice(chipToDelete, 1);
    this.setState({chipData: this.chipData});
  };

  handleAddChip = (record, i,j) => {
    this.chipData = this.state.chipData;
    this.schema = this.state.schema;

    
    // this.state.schema[i].items[j]
    //record.disabled = true;

    this.chipData.push(record);
    this.setState({chipData: this.chipData});
    this.setState({schema: this.schema});
  };

  handleToggleMenu = () => this.setState({
    open: !this.state.open
  });

  componentDidMount() {}

  renderChip(data) {

    return (
      <Chip key={data.value} onRequestDelete={() => this.handleRequestDelete(data)} style={this.styles.chip}>
        {data.name}
      </Chip>
    );
  }

  render() {

    const subcat = this.state.schema.map((record, i) => record.items.map((subrecord, j) => <ListItem key={j} disabled={subrecord.disabled} primaryText={subrecord.name} onTouchTap={() => this.handleAddChip(subrecord,i,j)} leftIcon={< ActionGrade />}/>));
    const listItems = this.state.schema.map((record, i) => <ListItem key={i} nestedItems={subcat[i]} primaryText={record.category} initiallyOpen={true} primaryTogglesNestedList={true}/>);

    return (
      <Wrapper>
        {/* <RaisedButton label="Toggle Drawer" onTouchTap={this.handleToggleMenu}/> */}
        <Drawer open={this.state.open}>
          <br/>
          <br/>
          <br/>
          <br/>
          <List>
            <Subheader>Filtros activos</Subheader>
            <div style={this.styles.wrapper}>
              {this.state.chipData.map(this.renderChip, this)}
            </div>
            {listItems}
          </List>
        </Drawer>
      </Wrapper>
    )
  }
}

export default FileSearchMenu
